import {
  Col, Container, Image, Row,
} from 'react-bootstrap';
import React, { Fragment } from 'react';

function isEven(value) {
  return value % 2 === 0;
}

function rightAlign(currentValue) {
  return (
    <Fragment>
      <Col xs={6}>
        <Image
          src={currentValue.image}
          fluid
        />
      </Col>
      <Col xs={6}>
        <h1>{currentValue.header}</h1>
        {currentValue.text}
      </Col>
    </Fragment>
  );
}

function leftAlign(currentValue) {
  return (
    <Fragment>
      <Col xs={6}>
        <h1>{currentValue.header}</h1>
        {currentValue.text}
      </Col>
      <Col xs={6}>
        <Image
          src={currentValue.image}
          fluid
        />
      </Col>
    </Fragment>
  );
}

export default ({indexContentData}) => (
  <Container>
    {
      indexContentData.map((currentValue, index) => (
        <Row style={{ paddingTop: '40px' }}>
          {
            isEven(index) ? rightAlign(currentValue) : leftAlign(currentValue)
          }
        </Row>
      ))
    }
  </Container>
);
