import React from 'react';
import { Image, Carousel } from 'react-bootstrap';

export default ({ customCarouselData }) => (
  <Carousel touch slide pauseOnHover>
    {
      customCarouselData.map(el => (
        <Carousel.Item>
          <Image
            src={el.img}
            alt={el.alt}
            fluid
          />
          <Carousel.Caption>
            <h3>{el.label}</h3>
            <p>{el.text}</p>
          </Carousel.Caption>
        </Carousel.Item>
      ))
    }
  </Carousel>
);
