import React from 'react';
import {
  Card,
  Container,
} from 'react-bootstrap';

export default ({ indexContentData }) => (
  <Container>
    {
      indexContentData.map(el => (
        <Card>
          <Card.Img variant="top" src={el.image} />
          <Card.Body>
            <Card.Title>{el.header}</Card.Title>
            <Card.Text>
              {el.text}
            </Card.Text>
          </Card.Body>
        </Card>
      ))
    }
  </Container>
);
